import React from 'react';
import ReactSpeedometer from "react-d3-speedometer";
import { Paper, Typography, Box } from '@mui/material';

const Gauge = ({ title, value, minValue, maxValue, units }) => {
  return (
    <Paper elevation={3} style={{ padding: '10px', margin: '5px' }}>
      <Typography variant="body1" gutterBottom>{title}</Typography>
      <Box display="flex" justifyContent="center">
        <ReactSpeedometer
          value={value}
          minValue={minValue}
          maxValue={maxValue}
          needleColor="steelblue"
          startColor="green"
          endColor="red"
          segments={10}
          textColor="#000"
          width={200}  // Adjust width for better fitting
          height={150} // Adjust height for better fitting
          ringWidth={30} // Adjust ring width if necessary
          segmentColors={['#00ff00', '#ffff00', '#ffa500', '#ff4500', '#ff0000']}
          currentValueText={`${value.toFixed(2)} ${units}`}
          valueTextFontSize="15px"
          labelFontSize="12px" // Ensure labels are readable
          needleTransitionDuration={4000}
          needleTransition="easeElastic"
          forceRender={true}
        />
      </Box>
      <Typography align="center" variant="caption">{`${value.toFixed(2)} ${units}`}</Typography>
    </Paper>
  );
};

export default Gauge;




