import React, { useEffect, useState } from 'react';
import './App.css';
import Gauge from './components/Gauge';

function App() {
  const [data, setData] = useState({
    mode: '',
    sensor_data: {},
    control_signals: {},
  });

  useEffect(() => {
    const connectWebSocket = () => {
      const ws = new WebSocket('ws://localhost:8080/ws');

      ws.onmessage = function (event) {
        const receivedData = JSON.parse(event.data);
        setData(receivedData);
      };

      ws.onclose = function () {
        console.error('WebSocket connection closed, reconnecting...');
        setTimeout(connectWebSocket, 1000);
      };

      ws.onerror = function (error) {
        console.error('WebSocket error:', error);
        ws.close();
      };

      return () => ws.close();
    };

    connectWebSocket();
  }, []);

  return (
    <div className="App">
      <h1>Instrumentation Panel</h1>
      <div className="panel">
        <Gauge title="Heading" value={data.sensor_data.Heading || 0} minValue={0} maxValue={360} units="" />
        <Gauge title="Flight Mode" value={data.mode || 0} minValue={0} maxValue={2} units="" />
        <Gauge title="Pitch" value={data.sensor_data.Pitch || 0} minValue={-30} maxValue={30} units="" />
        <Gauge title="Roll" value={data.sensor_data.Roll || 0} minValue={-30} maxValue={30} units="" />
        <Gauge title="Altitude" value={data.sensor_data.Altitude || 0} minValue={0} maxValue={60000} units="ft" />
        <Gauge title="Leanness" value={data.sensor_data.Leanness || 0} minValue={0} maxValue={1.2} units="" />
        <Gauge title="Engine 1 RPM" value={data.sensor_data.Engine1RPM || 0} minValue={0} maxValue={6000} units="RPM" />
        <Gauge title="Prop Pitch 1" value={data.control_signals.PropPitch1 || 0} minValue={22} maxValue={45} units="" />
        <Gauge title="Fuel Tank 1" value={data.sensor_data.FuelTank1 || 0} minValue={0} maxValue={100} units="%" />
        <Gauge title="Engine 2 RPM" value={data.sensor_data.Engine2RPM || 0} minValue={0} maxValue={6000} units="RPM" />
        <Gauge title="Prop Pitch 2" value={data.control_signals.PropPitch2 || 0} minValue={22} maxValue={45} units="" />
        <Gauge title="Fuel Tank 2" value={data.sensor_data.FuelTank2 || 0} minValue={0} maxValue={100} units="%" />
        <Gauge title="Engine 3 RPM" value={data.sensor_data.Engine3RPM || 0} minValue={0} maxValue={6000} units="RPM" />
        <Gauge title="Prop Pitch 3" value={data.control_signals.PropPitch3 || 0} minValue={22} maxValue={45} units="" />
        <Gauge title="Engine 4 RPM" value={data.sensor_data.Engine4RPM || 0} minValue={0} maxValue={6000} units="RPM" />
        <Gauge title="Prop Pitch 4" value={data.control_signals.PropPitch4 || 0} minValue={22} maxValue={45} units="" />
      </div>
    </div>
  );
}

export default App;
